/* Minimal CSS, most styling will be handled by Tailwind */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Only add custom animations or utilities not covered by Tailwind */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.fade-in {
  animation: fadeIn 0.3s ease-in;
}
